.product-card {
  padding-top: 21px;
  padding-bottom: 25px;
}

.product-card__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  margin-bottom: 15px;
}

.product-card__link {
  @include fontStyle(16px, 20px);
  display: block;
  color: $mineShaft;
  text-decoration: none;
  text-transform: uppercase;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.product-card__link--picture {
  grid-row: 1 / 3;
  position: relative;
}

.product-card__image {
  display: block;
  position: absolute;
  top: 51%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.product-card__image--gramm_500 {
  top: 60%;
}

.product-card__image--gramm_1000 {
  top: 55%;
}

.product-card__title {
  @include fontStyle(16px, 20px);
  font-weight: 400;
  margin: 0;
  padding-right: 65px;
  grid-column: 2 / 3;
}

.product-card__list {
  grid-column: 2 / 3;
  grid-row: 2 / 3;
}

.product-card__button {
  @include fontStyle(16px, 20px);
  width: 100%;
  padding: 10px 0;
  font-family: inherit;
}

@media (min-width: $tablet-width) {
  .product-card {
    padding: 8px 37px 39px 39px;
    background: linear-gradient(to top, $basicGrey 430px, $white 430px 100%);
  }

  .product-card__wrapper {
    min-height: 391px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 33px;
  }

  .product-card__link--picture {
    width: 168px;
    height: 200px;
    margin-bottom: 43px;
  }

  .product-card__image {
    width: auto;
    height: auto;
    top: 62%;
  }

  .product-card__image--gramm_500 {
    top: 62%;
  }

  .product-card__image--gramm_1000 {
    top: 55%;
  }

  .product-card__title {
    @include fontStyle(20px, 24px);
    width: 174px;
    margin-bottom: 21px;
    padding: 0 20px;
    text-align: center;
  }

  .product-card__list {
    align-self: stretch;
  }
}

@media (min-width: $desktop-width) {
  .product-card {
    padding: 0 38px 40px 39px;
    background: linear-gradient(to top, $basicGrey 431px, $white 431px 100%);
  }

  .product-card__link--picture {
    margin-bottom: 39px;
  }

  .product-card__title {
    @include fontStyle(20px, 21px);
    width: 160px;
    padding: 0 15px;
  }

  .product-card__image {
    width: auto;
    height: auto;
    top: 54%;
  }

  .product-card__image--gramm_500 {
    top: 53%;
  }

  .product-card__image--gramm_1000 {
    top: 47%;
  }
}
